.movie, .movies
{
  background-color: white;
  margin: 30px;
  padding: 20px;
  border-radius: 5px;
  color: #adaeb9;
  box-shadow: 
  0 13px 27px -5px rgba(50, 50, 93, .25),
  0 8px 16px -8px rgba(50, 50, 93, .25),
  0 -6px 16px -6px rgba(50, 50, 93, .25);
}

.movie img
{
  position: relative;
  top: -50px;
  max-width: 150px;
  box-shadow: 
  0 13px 27px -5px rgba(50, 50, 93, .25),
  0 8px 16px -8px rgba(50, 50, 93, .25),
  0 -6px 16px -6px rgba(50, 50, 93, .25);
}
.movie .movie_year
{
  margin: 0;
  font-weight: 300;
}

.movie .movie_title
{
  margin: 0;
  font-size: 24px;
  color: #2c2d2c;
}

.movie .movie_genres
{
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  margin: 5px 0px;
}
.movie .movie_genre
{
  margin: 0px 5px;
}

.movie>a{
  text-decoration: none;
  color: inherit;
}